.banner{
  color: #650300;
  height: calc(100vh - 109px);
  background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner.png?raw=true);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.banner .container{
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

@media (max-width:992px){
  .banner{
    background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner-sm.png?raw=true);
  }
}
@media (max-width:767px){
  .banner{
    height: 394px;
  }
}

.intro{
  background: #AA06010D;
  padding-top: 60px;
  padding-bottom: 80px;
}

.intro-list{
  display: flex;
  justify-content: space-between;
}

.intro-card{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 255px;
  margin: 0 15px;
}

.intro-card-icon{
  width: 84px;
  height: 84px;
}

.intro-card-title{
  color:   #650300;
  font-weight: bold;
  font-size: 24px;
  text-align: center;
  margin: 24px 0 8px;
}

@media (max-width:992px){
  .intro{
    padding-top: 40px;
  }
  .intro-list{
    flex-wrap: wrap;
  }
  .intro-card{
    width: 45%;
    margin: 0 0;
  }
}

@media (max-width:767px){
  .intro{
    padding-top: 32px;
    padding-bottom: 0;
  }
  .intro h2{
    margin-bottom: 28px;
  }
  .intro-card{
    width: 100%;
    margin-bottom: 40px;
  }
}

.classic{
  padding-top: 60px;
  padding-bottom: 48px;
}

.classic-list-item{
  display: flex;
  align-items: center;
  margin-bottom: 32px;
}

.classic-list-item:nth-child(even){
  flex-direction: row-reverse;
}

.classic-list-item-text{
  display: flex;
  align-items: center;
}

.classic-photo{
  width: 57%;
}

.mr-30{
  margin-right: 30px;
}

.ml-30{
  margin-left: 30px;
}

.classic-list-item-text p{
  font-size: 48px;
  color: #AA0601;
  font-family: "Roboto", serif;
  font-weight: 900;
  font-style: italic;
}

.classic-icon{
  width: 44px;
  height: 44px;
  margin-right: 10px;
}

@media (max-width:992px){
  .classic-list-item{
    margin-bottom: 8px;
  }
  .classic-photo{
    margin-right: 32px;
    margin-left: 0;
  }

  .classic-list-item:nth-child(even){
    flex-direction: row;
  }

  .classic-icon{
    width: 37px;
    height: 37px;
  }

  .classic-list-item-text p{
    font-size: 28px;
  }
}

@media (max-width:767px){
  .classic{
    padding-top: 32px;
    padding-bottom: 40px;
  }
  .classic-photo{
    margin: 0 auto;
    width: 100%;
  }
  .classic-list-item{
    position: relative;
    justify-content: center;
  }
  .classic-list-item-text{
    position: absolute;
  }
}

.co-branding{
  background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/bg.png?raw=true);
  background-position: center center;
  background-repeat: no-repeat;
  padding-top: 60px;
  padding-bottom: 80px;
}

.co-branding h2{
  color: #fff;
}

.co-branding-list{
  display: flex;
  justify-content: space-between;
}

.co-branding-list-item{
  color: #AA0601;
  font-size: 72px;
  font-family: "Roboto", serif;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 49%;
}

.co-branding-list-item:before{
  content: "";
  background-color: rgba(255,255,255,0.5);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.co-branding-list-item p{
  position: absolute;

}

@media (max-width:992px){
  .co-branding{
    padding-top: 40px;
    padding-bottom: 56px;
  }
  .co-branding-list-item{
    font-size: 32px;
  }
}

@media (max-width:767px){
  .co-branding{
    padding-top: 32px;
    padding-bottom: 40px;
  }
  .co-branding h2{
    margin-bottom: 20px;
  }
  .co-branding-list{
    flex-wrap: wrap;
  }
  .co-branding-list-item{
    width: 100%;
  }
  .co-branding-list-item:nth-child(1){
    margin-bottom: 8px;
  }
}

.testimonial{
  padding-top: 60px;
  padding-bottom: 80px;
}

.testimonial-list{
  display: flex;
  justify-content: center;
}

.testimonial-card{
  border: 1px solid #DEE2E6;
  border-radius: 4px;
  box-shadow: 0px 2px 6px #00000029;
  width: 255px;
  margin: 0 15px;
}

.testimonial-card-date{
  color: #707070;
  font-size: 14px;
}

.testimonial-card-bottom{
  height: 204px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 16px;
}

.tesimonial-card-name{
  margin-bottom: 8px;
}

@media (max-width:992px){
  .testimonial{
    padding-top: 40px;
    padding-bottom: 24px;
  }

  .testimonial-list{
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .testimonial-card{
    width: 45%;
    margin-bottom: 32px;
  }
}

@media (max-width:767px){
  .testimonial{
    padding-top: 32px;
    padding-bottom: 40px;
  }
  .testimonial h2{
    margin-bottom: 20px;
  }
  .testimonial-card{
    width: 100%;
    margin-bottom: 16px;
  }
}

.contact{
  background: #AA06010D;
  padding-top: 60px;
  padding-bottom: 80px;
}

.contact p{
  font-size: 20px;
  margin-bottom: 20px;
}

.contact label{
  margin-bottom: 4px;
}

.contact-normal-input{
  margin-bottom: 16px;
  background: #AA06010D;
  width: 100%;
  height: 50px;
}

.confidential-info-policy{
  display: flex;
  margin-bottom: 40px;
}

.contact-btn{
  background: #AA0601;
  color: #fff;
  padding: 16px 143px;
  font-weight: bold;
}

.contact-form-btn{
  display: flex;
  justify-content: center;
}

::placeholder {
  color: #00000051;
}

input[type="text"], input[type="tel"], input[type="email"], textarea{
  padding: 14px 16px;
}

@media (max-width:992px){
  .contact-btn{
    padding: 10px 77px;
  }
}

@media (max-width:767px){
  .contact{
    padding-top: 30px;
    padding-bottom: 48px;
  }
  .contact-form p{
    font-size: 16px;
  }
  .contact-btn{
    padding: 8px 55px;
  }
  .confidential-info-policy{
    font-size: 14px;
    margin-bottom: 16px;
  }
}