.header{
  background: #AA0601;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 88px;
}

.logo a{
  background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/logo.png?raw=true);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: block;
  width: 80px;
  height: 49px;
  text-indent: 101%;
  white-space: nowrap;
  overflow: hidden;
} 

.nav{
  display: flex;
}

.nav-items{
  padding-left: 64px;
  font-size: 20px;
}

@media (max-width:992px){
  .header{
    padding: 30px 39px;
  }  
  .nav-items{
    padding-left: 40px;
  }
}

@media(max-width:767px){
  .header{
    padding: 0;
    flex-direction: column;
  }
  .nav{
    width: 100%;
    flex-wrap: wrap;
  }
  .nav-items{
    padding: 0;
    border: 1px solid #fff;
    width: 50%;
    text-align: center;
    padding-top: 8px;
    padding-bottom: 8px;
  }
  .logo a{
    width: 48px;
    height: 29px;
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

//footer
.footer{
  background: #AA0601;
  color: #fff;
  padding-top: 32px;
  padding-bottom: 39px;
}

.footer-content{
  display: flex;
  justify-content: space-between;
  align-items: start;
  padding-bottom: 36px;
  border-bottom: 1px solid #fff;
}

.footer-nav{
  display: flex;
  margin-bottom: 36px;
}

.footer-nav-item{
  margin-right: 48px;
}

.footer-social-media{
  display: flex;
}

.footer-social-media-icon{
  margin-left: 32px;
  width: 48px;
  height: 48px;
}

.footer-contact-icon{
  width: 36px;
  height: 36px;
  margin-right: 22px;
}

.footer-contact-info-item a{
  font-size: 36px;
  font-family: "Roboto", serif;
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}

.footer-bottom{
  display: flex;
  justify-content: space-between;
  margin-top: 32px;
}

.footer-policy{
  display: flex;
}

.footer-policy-item{
  margin-left: 48px;
}

@media (max-width:992px){
  .footer{
    padding-bottom: 32px;
  }

  .footer-content{
    padding-bottom: 8px;
  }

  .footer-nav{
    margin-bottom: 18px;
  }

  .footer-nav-item{
    margin-right: 40px;
  }

  .footer-social-media-icon{
    margin-left: 24px;
    width: 24px;
    height: 24px;
  }

  .footer-contact-icon{
    width: 18px;
    height: 18px;
    margin-right: 11px;
  }

  .footer-contact-info-item a{
    font-size: 20px;
    margin-bottom: 12px;
  }
}

@media (max-width:767px){
  .footer{
    padding-top: 25px;
    padding-bottom: 16px;
  }
  .footer-content{
    padding-bottom: 18px;
  }
  .footer-nav{
    display: none;
  }
  .footer-contact-icon{
    width: 15px;
    height: 15px;
    margin-right: 4px;
  }
  .footer-contact-info-item a{
    font-size: 16px;
    margin-bottom: 6px;
  }
  .footer-bottom{
    display: block;
    margin-top: 16px;
  }
  .footer-policy{
    display: block;
  }
  .footer-policy-item{
    margin-left: 0;
  }
  .footer-bottom p, .footer-policy-item{
    margin-bottom: 8px;
    font-size: 14px;
  }
}